<template>
    <div class="service-entry">
        <div class="service-entry-header">
            <div class="left service-entry-id">
                {{ id }}
            </div>
            <div>
                <button class="service-entry-button button right margin-left" @click="deleteService(id)">
                    <font-awesome-icon icon="trash-alt"/>
                </button>
                <button class="service-entry-button button right" @click="editService(id)">
                    <font-awesome-icon icon="cog"/>
                </button>
            </div>
        </div>
        <transition name="fade">
            <div class="service-entry-config" v-if="showEditService">
                <form @submit.prevent="testConfiguration">
                    <table>
                        <tr>
                            <td><label for="type-input">Type</label></td>
                            <td>
                                <select id="type-input" v-model="serviceConfiguration.type">
                                    <option value="com.arassec.igor.module.ftp.service.FtpFileService">FTP</option>
                                    <option value="com.arassec.igor.module.ftp.service.FtpsFileService">FTPS</option>
                                    <option value="com.arassec.igor.module.localfs.service.LocalFilesystemFileService">
                                        Filesystem
                                    </option>
                                    <option value="com.arassec.igor.module.ssh.service.ScpFileService">SCP</option>
                                    <option value="com.arassec.igor.module.ssh.service.SftpFileService">SFTP</option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <td><label for="host-input">Host</label></td>
                            <td><input id="host-input" type="text" autocomplete="off"
                                       v-model="serviceConfiguration.parameters.host"/></td>
                        </tr>
                        <tr>
                            <td><label for="port-input">Port</label></td>
                            <td><input id="port-input" type="text" autocomplete="off"
                                       v-model.number="serviceConfiguration.parameters.port"
                                       number/></td>
                        </tr>
                        <tr>
                            <td><label for="username-input">Username</label></td>
                            <td><input id="username-input" type="text" autocomplete="off"
                                       v-model="serviceConfiguration.parameters.username"/></td>
                        </tr>
                        <tr>
                            <td><label for="password-input">Password</label></td>
                            <td><input id="password-input" type="password" autocomplete="off"
                                       v-model="serviceConfiguration.parameters.password"/></td>
                        </tr>
                        <tr>
                            <td colspan="2">
                                <button class="button right">Test configuration</button>
                            </td>
                        </tr>
                    </table>
                    <div v-if="showTestResult && testOk" class="feedbackbox">
                        {{ testResult }}
                    </div>
                    <div v-if="showTestResult && !testOk" class="alertbox">
                        {{ testResult }}
                    </div>
                </form>
                <div v-if="showSaveResult && !saveOk" class="alertbox">
                    {{ saveResult }}
                </div>
                <button class="button right" @click="saveConfiguration">Save configuration</button>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "ServiceListEntry",
        props: ['id'],
        data: function () {
            return {
                showEditService: false,
                showTestResult: false,
                testResult: '',
                testOk: true,
                showSaveResult: false,
                saveResult: '',
                saveOk: true,
                serviceConfiguration: {
                    id: '',
                    type: 'com.arassec.igor.module.ftp.service.FtpFileService',
                    parameters: {
                        host: 'test.rebex.net',
                        port: 21,
                        username: 'demo',
                        password: 'password'
                    }
                }
            }
        },
        methods: {
            editService: function (id) {
                this.showEditService = !this.showEditService;
            },
            deleteService: function (id) {
                var component = this;
                this.$http.delete(this.$igorUrl + "/api/service/" + id)
                    .then(function (response) {
                        component.loadExistingServices();
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
            },
            testConfiguration: function () {
                var component = this;
                this.$http.post(this.$igorUrl + "/api/service/test", this.serviceConfiguration
                ).then(function (response) {
                    component.testResult = response.data;
                    component.showTestResult = true;
                    component.testOk = true;
                }).catch(function (error) {
                    component.testResult = error.response.data;
                    component.showTestResult = true;
                    component.testOk = false;
                });
            },
            saveConfiguration: function () {
                var component = this;
                this.$http.post(this.$igorUrl + "/api/service", this.serviceConfiguration
                ).then(function (response) {
                    component.$emit('save')
                }).catch(function (error) {
                    component.saveResult = error.response.data;
                    component.showSaveResult = true;
                    component.saveOk = false;
                });
            }
        },
    }
</script>

<style scoped>

    .service-entry {
        padding: 15px 15px 15px 15px;
        background-color: var(--panel-background-color);
        overflow: auto;
        border-bottom: 5px solid var(--main-background-color);
    }

    .service-entry-config {
        margin-top: 40px;
    }

    .service-entry-id {
        margin-top: 2px;
        font-weight: bold;
    }

    .service-entry-button {
        margin-top: -1px;
    }

    .service-entry table tr {
        line-height: 30px;
    }

    .service-entry table td {
        text-align: left;
        padding-right: 15px;
    }

    .service-entry table tr td label {
        color: var(--font-color-light);
        margin-right: 15px;
    }

    .service-entry table tr td input, select {
        border: none;
        background-color: var(--element-background-color);
        height: 25px;
        min-width: 300px;
        color: var(--font-color-light);
    }

    .service-entry table tr td input {
        padding-left: 5px;
    }

    .service-entry table tr td select option {
        border: none;
        outline: none;
        background-color: var(--element-background-color);
        height: 25px;
    }

    .service-entry table tr td select:focus,
    .service-entry table tr td input:focus {
        background-color: var(--element-background-color-focus);
        color: var(--panel-background-color);
        outline: none;
    }

</style>